dth:300px}.Content p{line-height:200%;padding-bottom:22px}.Content .hero-image{min-height:220px;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover;display:flex;flex-direction:column;color:white}.Content .hero-image .hero-image-inner{width:100%;height:100%;display:flex;flex-direction:column}.Content .hero-image .hero-image-inner h1{margin-top:64px;text-shadow:2px 2px 10px black}.Content .hero-image .hero-image-inner *{align-self:center}.Content li{padding-bottom:24px}.flex-row{display:flex;flex-direction:row}.flex-column{display:flex;flex-direction:column}.bold{font-weight:bold}@media (max-width: 930px){.sm-col{flex-direction:column}}.PermitMap-holder{width:100%;display:flex;flex-direction:column}.PermitMap-holder *{align-self:center}.PermitMap-holder svg{width:100%}.PermitMap-holder .legend{display:inline-block;width:32px;height:14px;border:1px solid #222;border:1px solid var(--color-black);margin-bottom:-2px}.PermitMap-holder .legend.valid{background-color:#ffcc00;background-color:var(--color-gold)}.PermitMap-holder .legend.invalid{background-color:#cd2500;background-color:var(--color-red)}

